import React, {useEffect} from 'react';
import {Switch, Route, withRouter} from 'react-router-dom';

import HeaderComponent from './components/Layout/HeaderComponent/HeaderComponent';
// import MainComponent from "./containers/MainComponent/MainComponent";
// import LayersWidget from "./containers/LayersWidget/LayersWidget";
// import AsideComponent from "./components/Layout/AsideComponent/AsideComponent";

import Spinner from "./components/UI/Spinner/Spinner";
// import * as ActionTypes from "./store/actionTypes";
// import {connect} from 'react-redux';

import './App.css';
// import EditorComponent from "./components/EditorComponent/EditorComponent";
import DemoComponent from "./containers/DemoComponent/DemoComponent";
import HomeComponent from "./containers/HomeComponent/HomeComponent";
// import AsideComponent from "./components/Layout/AsideComponent/AsideComponent";

const title = "Debug Viewer";
const version = process.env.REACT_APP_VERSION;

const App = (props) => {
    useEffect( () => {
        window.addEventListener('beforeunload', function (e) {
            e.preventDefault();
            e.returnValue = '';
        });
    },[]);

    return (
        <div className="App">
            <HeaderComponent
                title={title}
                version={version}
            />

            {/*<DocumentName name={props.document.name}*/}
            {/*username={props.username}*/}
            {/*updateDocumentName={props.updateDocumentName} />*/}

            {/*<div className="App-body" onPaste={props.pasteDataFromBuffer}>*/}
            {/*    <MainComponent />*/}
            {/*    <LayersList />*/}
            {/*    <AsideComponent/>*/}
            {/*</div>*/}


            <Switch>
                <Route exact path="/">
                    <HomeComponent />
                    {/*<EditorComponent />*/}
                </Route>
                <Route exact path="/demo" >
                    <DemoComponent />
                </Route>
            </Switch>

            {props.app?.showSpinner ? <Spinner/> : null}
        </div>
    );
}

// const mapDispatchToProps = dispatch => {
//     return {
//         pasteDataFromBuffer: (event) => dispatch({
//             type: ActionTypes.DATA_FROM_BUFFER_PASTED,
//             data: event.clipboardData
//         })
//     }
// };

export default withRouter(App);
