import React, {useEffect, useState} from 'react';
import EditorComponent from "../../components/EditorComponent/EditorComponent";
// import styles from "./DemoComponent.module.css";
import {parseTXT} from "../../dataParsers/parseTXT";
import Layers from "../../models/layers";

let url234 = "https://gist.githubusercontent.com/alexbol99/e570fe783be88b556f4ca4b332da73f7/raw/741c87a7ceccdeadd9fbbac05bdfda22ebe94927/poly234.txt";

const DemoComponent = () => {
    const [layers,setLayers] = useState([]);

    useEffect( () => {
        const fetchDemo = async () => {
            const resp = await fetch(url234);
            const data = await resp.text();
            const job = parseTXT("layer"/*layer.name*/, data);

            setLayers(prevLayers => {
                const layer = Layers.newLayer(null, prevLayers);
                layer.color = Layers.getNextColor(prevLayers);
                layer.displayed = true;
                layer.affected = true;
                for (let shape of job.shapes) {
                    layer.add(shape);
                }
                const newLayers = [...prevLayers, layer];
                return newLayers;
            });
        }

        fetchDemo()
            .catch(console.error)

    },[])

    const clearCurrentDocument = () => {
        setLayers([])
        // this.props.history?.push("/");
    };

    return (
        <EditorComponent
            onClearAllButtonClicked={clearCurrentDocument}
        >
            {layers}
        </EditorComponent>
    );
};

export default DemoComponent;
